import React, {Component} from 'react';
import axios from 'axios';
import './Filter.css';

const restdetail = "https://restaurants-clone.herokuapp.com/restaurantList";
class CusineFilter extends Component {

    cuisineFilter = (event) => {
        let mealid = (this.props.mealIdNumber)
        let cuisine = (event.target.value)
        
        let url;
        if(cuisine == ""){
            url = `${restdetail}/${mealid}`
        }else{
            url = `${restdetail}/${mealid}?cuisine=${cuisine}`
        }
        axios.get(url)
        .then((response) => {this.props.restpercuisine(response.data)})
    }

    render(){
        return(
            <React.Fragment>
               <div className="couisine-style">
                    
                    <div className="couisine-style"> 
                        couisine
                    </div>
                    <div onChange={this.cuisineFilter}>
                        <div className="radio">
                            <label><input type="radio" value="" name="radio"/>All</label>
                        </div>
                        <div className="radio">
                            <label><input type="radio" value="1" name="radio"/>North Indian</label>
                        </div>
                        <div className="radio">
                            <label className="radio"><input type="radio" value="2" name="radio"/>South Indian</label>
                        </div>
                        <div className="radio">
                            <label className="radio"><input type="radio" value="3" name="radio"/>Chinese</label>
                        </div>
                        <div className="radio">
                            <label className="radio"><input type="radio" value="4" name="radio"/>Fast Food</label>
                        </div>    
                        <div className="radio">
                            <label className="radio"><input type="radio" value="5" name="radio"/>Street Food</label>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )               
    }
}


export default CusineFilter