import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import './RestDetail.css'

const url='https://restaurants-clone.herokuapp.com/restaurant?id='

class RestDetail extends Component {
    constructor () {
        super ()

        this.state={
            restaurant:{
                "Cuisine":[
                    {"name":""},
                    {"name":""}
                ]
            }
        }
    }

    onBack = () => {
        var mealId=sessionStorage.getItem('type');
        this.props.history.push(`/list/${mealId}`)

    }

    render() {
        console.log(this.props.match.params.id);
        var {restaurant} = this.state;       
        return (
            <div className="container">
                <div className="panel panel-primary">
                            <div className="panel-heading">
                                <h2>{restaurant.name}</h2>
                            </div>
                            <div className="panel-body">
                                <div className="row">
                                    <div className="col-md-12">
                                    <div id="myCarousel" className="carousel slide" data-ride="carousel">

                                        <ol className="carousel-indicators">
                                            <li data-target="#myCarousel" data-slide-to="0" className="active"></li>
                                            <li data-target="#myCarousel" data-slide-to="1"></li>
                                            <li data-target="#myCarousel" data-slide-to="2"></li>
                                        </ol>

                                        <div className="carousel-inner">
                                            <div className="item active">
                                            <img src={restaurant.thumb}/>
                                            </div>

                                            <div className="item">
                                            <img src={restaurant.thumb}/>
                                            </div>

                                            <div className="item">
                                            <img src={restaurant.thumb}/>
                                            </div>
                                        </div>


                                        <a className="left carousel-control" href="#myCarousel" data-slide="prev">
                                            <span className="glyphicon glyphicon-chevron-left"></span>
                                            <span className="sr-only">Previous</span>
                                        </a>
                                        <a className="right carousel-control" href="#myCarousel" data-slide="next">
                                            <span className="glyphicon glyphicon-chevron-right"></span>
                                            <span className="sr-only">Next</span>
                                        </a>
                                        </div>
                                        
                                    </div>
                                    <div className="col-md-12">
                                        <h4 className="heading">{restaurant.name}</h4>
                                        <p className="headingsub1">{restaurant.address}</p>
                                        <p className="headingsub1">{restaurant.locality}</p>
                                    </div>
                                </div>
                                <br/>
                                <div>
                                    <Tabs>
                                        <TabList>
                                            <Tab className="heading2">Overview</Tab>
                                            <Tab className="heading2">Contact</Tab>
                                        </TabList>
                                        <TabPanel>
                                            <div>
                                                
                                                <br/>
                                                <div className="headingsub">Cuisine</div>
                                                <div className="heading-no">{restaurant.Cuisine[0].name}, {restaurant.Cuisine[1].name}</div>
                                                <br/>
                                                <div className="heading-p">Cost For 2 :  Rs. {restaurant.cost}</div>
                                            </div>
                                        </TabPanel>
                                        <TabPanel>
                                        <div>
                                                <br/>
                                                <div className="headingsub">Phone</div>
                                                <div className="heading-no">45352465</div>
                                                <br/>
                                                <div className="heading-p">{restaurant.locality}</div>
                                            </div>
                                        </TabPanel>
                                    </Tabs>
                                </div>
                                <br/><br/>
                                <div>
                                    <button className="btn btn-danger btn-lg" onClick={this.onBack}>Back</button>
                                    &nbsp;
                                    <Link className="btn btn-success btn-lg" to={`/order/${this.props.match.params.id}`}>Place Order</Link>
                                </div>
                            </div>
                        </div>
            </div>
        )
    }

    componentDidMount () {
        var restId=this.props.match.params.id;
        fetch(`${url}${restId}`,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            console.log(data)
            this.setState({restaurant:data[0]})
        })
    }
}

export default RestDetail
