import React,{Component} from 'react';
import Home_quickSearchDisplay from './Home_quickSearchDisplay'
import './Home_quicksearch.css'


const url = "https://restaurants-clone.herokuapp.com/mealtype";

class Home_quickSearch extends Component{
    constructor(){
        super();

        this.state={
            mealtype:''
        }
    }
    render(){
        return (
            <div>
                <Home_quickSearchDisplay mealtype={this.state.mealtype} />
            </div>
        )
    }
    componentDidMount(){
        fetch(url,{method:'GET'})
        .then((res)=> res.json())
        .then((data)=>{
            this.setState({mealtype:data})
        })
    }
}


export default Home_quickSearch
