import React,{Component} from 'react';
import './Home_search.css'


const url="https://restaurants-clone.herokuapp.com/city";
const rurl="https://restaurants-clone.herokuapp.com/restaurant?city="

class Home_search extends Component{
    constructor(){
        super();

        this.state={
            location:'',
            restaurant:''
        }
    }

    renderCity = (data) => {
        if (data) {
            return data.map((item) => {
                    return (
                        <option value={item.city}>{item.city_name}</option>
                    )
                }
            )
        }
    }

    renderRestaurant = (data) => {
        if (data) {
            return data.map((item) => {
                return (
                <option value={item._id}>{item.name}, {item.locality}</option>
                )
            })
        }
    }

    handleCity = (event) => {
        console.log(event.target.value)
        fetch(`${rurl}${event.target.value}`,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            console.log(data);
            this.setState({restaurant:data})
        })
    }

    handlerest =(event) => {
        this.props.rid(event.target.value)
    }

    render(){
        return(
            <div className="container-fluid">
                <div className="jumbotron text-center">

                    <div className="logo">
                        <h2 className="mh">e!</h2>

                    </div>
                    <p className="subtitle">
                        find the best restaurant, cafes and bars
                    </p>

                    <div className="btn-group">
                        <select className="input-lg input-sm form-control cursor" onChange={this.handleCity}>
                            <option disabled selected>Select City</option>
                            {this.renderCity(this.state.location)}
                        </select>
                    </div>

                    <div className="btn-group display">
                        <button className="btn btn-default input-lg input-sm">
                            <i className="glyphicon glyphicon-search"></i>
                        </button>
                        <select className="input-lg input-sm form-control cursor" onChange={this.handlerest}>
                            <option disabled selected>Select Restaurant</option>
                            {this.renderRestaurant(this.state.restaurant)}
                        </select>
                    </div>

                </div>
            </div>
        )
    }
    
    componentDidMount(){
        fetch(url,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            console.log(data)
            this.setState({location:data})
        })
    }
}


export default Home_search
