import React, {Component} from 'react';
import axios from 'axios';
import './Filter.css';

const restdetail = "https://restaurants-clone.herokuapp.com/restaurantList";
class SortFilter extends Component {
    
    sortFilter = (event) => {
        let mealid =(this.props.mealIdNumber);
        let sort = (event.target.value)
        let url = `${restdetail}/${mealid}?sort=${sort}`;

        axios.get(url)
        .then((response) => {this.props.restsort(response.data)})
    }

    render(){
        return(
            <React.Fragment>
                <div onChange={this.sortFilter}>                                                                                
                    <div className="couisine-style">
                        Sort
                    </div>
                    <div>
                        <div className="radio">
                            <label><input type="radio" value="" name="sort"/>Price low to high</label>
                        </div>
                        <div className="radio">
                            <label><input type="radio" value="-1" name="sort"/>Price high to low</label>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )               
    }
}


export default SortFilter