import React from 'react';
import Home_search from './Home_search';
import Home_quickSearch from './Home_quickSearch'

function Home(props) {
    const handlerest = (data) => {
        props.history.push(`/detail/${data}`)
    }

    return (
        <div>
            <Home_search rid={(data) => {handlerest(data)}}/>
            <Home_quickSearch/>
        </div>
    )
}

export default Home
