import React,{Component} from 'react';
import RestListDisplay from './RestListDisplay';
import CostFilter from './Filter/CostFilter';
import CuisineFilter from './Filter/CuisineFilter';
import SortFilter from './Filter/SortFilter';
import CityFilter from './Filter/CityFilter'

const url="https://restaurants-clone.herokuapp.com/restaurant?mealtype="

class RestListApi extends Component{
    constructor(props){
        super(props)

        this.state={
            restaurant:''
        }
    }

    setDataPerFilter(sortedData){
        this.setState({restaurant:sortedData})
    }

    render(){
        console.log(this.props.match.params.id);
        return(
            <div className="container">
                
               <div className="col-sm-4 col-md-3 col-lg-3">
                   <div className="filter-box">
                        <center className="toggle" data-toggle="collapse" data-target="#filter">FILTER</center>   
                        <div id="filter" className="collapse navbar-collapse">
                            <CityFilter restpercity={(data) => {this.setDataPerFilter(data)}} mealIdNumber={this.props.match.params.id}/>
                            <CuisineFilter mealIdNumber={this.props.match.params.id} restpercuisine={(data) => {this.setDataPerFilter(data)}}/>
                            <CostFilter restpercost={(data) => {this.setDataPerFilter(data)}} mealIdNumber={this.props.match.params.id}/>
                            <SortFilter restsort={(data) => {this.setDataPerFilter(data)}} mealIdNumber={this.props.match.params.id}/>                           
                        </div>
                    </div>                 
               </div>
               <div className="col-sm-8 col-md-9 col-lg-9">
                   <RestListDisplay restaurant={this.state.restaurant} />
               </div>
            </div>
        )
    }

    componentDidMount(){
        var mealId=this.props.match.params.id;
        sessionStorage.setItem('type',mealId);
        fetch(`${url}${mealId}`,{method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            console.log(data)
            this.setState({restaurant:data})
        })
    }
}



export default RestListApi
