import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';
import Home from './Home/Home';
import Header from './Header/Header';
import Footer from './Footer';
import RestList from './RestListing/RestListApi';
import RestDetail from './RestDetail/RestDetail';
import Form from './Order/Form';
import Order from './Order/OrderApi'

function Routing() {
    return (
        <BrowserRouter>
            <div>
                <Header/>
                <Route exact path='/' component={Home}></Route>
                <Route exact path='/list/:id' component={RestList}></Route>
                <Route path='/detail/:id' component={RestDetail}></Route>
                <Route path='/order/:id' component={Form}></Route>
                <Route exact path="/vieworder" component={Order}></Route>
                <Footer/>
            </div>
        </BrowserRouter>
    )
}

export default Routing
