import React, {Component} from 'react';
import axios from 'axios';
import './Filter.css';

const restdetail = "https://restaurants-clone.herokuapp.com/restaurantList";
class CostFilter extends Component {
    
    costFilter = (event) => {
        let mealid =(this.props.mealIdNumber)
        let cost = (event.target.value).split(',');
        let lcost = Number(cost[0]);
        let hcost = Number(cost[1]);
        let url;
        if(event.target.value==""){
            url = `${restdetail}/${mealid}`
            console.log(url)
        }else{
            url = `${restdetail}/${mealid}?lcost=${lcost}&hcost=${hcost}`
            console.log(url)
        }
        axios.get(url)
        .then((response) => {this.props.restpercost(response.data)})
    }

    render(){
        return(
            <React.Fragment>
                <div>                                                                                                                                       
                    <div className="couisine-style">Cost of Two</div>
                    <div onChange={this.costFilter}>
                        <div className="radio">
                            <label><input type="radio" value="" name="radio"/>All</label>
                        </div>
                        <div className="radio">
                            <label><input type="radio" value="0,300" name="radio"/>Less than 300</label>
                        </div>
                        <div className="radio">
                            <label><input type="radio" value="301,500" name="radio"/>300 to 500</label>
                        </div>
                        <div className="radio">
                            <label><input type="radio" value="501,700" name="radio"/>500 to 700</label>
                        </div>
                        <div className="radio">
                            <label><input type="radio" value="701,2000" name="radio"/>Greater than 700</label>
                        </div>
                    </div>                  
                </div>
            </React.Fragment>
        )               
    }
}


export default CostFilter