import React, {Component} from 'react';
import axios from 'axios';
import './Filter.css';

const restdetail = "https://restaurants-clone.herokuapp.com/restaurantList";
const curl = "https://restaurants-clone.herokuapp.com/city";


class CityFilter extends Component {
    constructor(){
        super();

        this.state={
            city:''
        }
    }

    renderCity = (data) => {
        if (data) {
            return data.map((item) => {
                    return (
                        <option value={item.city}>{item.city_name}</option>
                    )
                }
            )
        }
    }

    componentDidMount(){
        axios.get(curl)
        .then((response) => {this.setState({city:response.data})})
    }
    
    cityFilter = (event) => {
        let mealid =(this.props.mealIdNumber);
        let city = (event.target.value)
        let url = `${restdetail}/${mealid}?city=${city}`;

        axios.get(url)
        .then((response) => {this.props.restpercity(response.data)})
    }

    render(){
        return(
            <React.Fragment>
                <div onChange={this.cityFilter}>                                                                                
                    <div className="form-group couisine-style">
                        <select class="form-control">
                            <option value="">Select Location</option>
                            {this.renderCity(this.state.city)}
                        </select>
                    </div>
                </div>
            </React.Fragment>
        )               
    }
}


export default CityFilter