import React from 'react';
import { Link } from 'react-router-dom';
import './RestListDisplay.css'

function RestListDisplay(props) {

    const RenderRest=({restaurant})=>{
        if (restaurant) {
            if(restaurant.length>0){
                return(
                    restaurant.map((item) => {
                        return (
                            <Link to={`/detail/${item._id}`}>
                            <div className="item-box1">
                            <div className="row">
                                <div class="col-xs-5 col-sm-5 col-md-3 col-lg-3">
                                    <img src={item.thumb} alt="snack-image" class="img-responsive img-style"/>
                                </div>
                                <div class="col-xs-7 col-sm-7 col-md-9 col-lg-9">
                                    <div class="mg-in-item-box-text">
                                        <div class="pd-item-hd">
                                            <b class="item-heading">{item.name}</b>
                                        </div>
                                        <div>
                                            <b class="item-subheading">{item.city_name}</b>
                                        </div>
                                        <div class="item-address">
                                            {item.address}
                                        </div>
                                    </div>
                                </div>
                            </div>
                                <hr/>
                            <div className="row">
                                <div class="col-xs-4 col-sm-4 col-md-3 col-lg-3 rs1">
                                    <p>CUISINE:</p>
                                    <p>
                                        Cost of Two
                                    </p>
                                </div>
                                <div class="col-xs-8 col-sm-8 col-md-9 col-lg-9 rs2">
                                    <p>{item.Cuisine[0].name}, {item.Cuisine[1].name}</p>
                                    <p>Rs. {item.cost}</p>
                                </div>
                            </div>

                            </div>
                            </Link>
                        )
                    })
                )
            } else{
                return(
                    <div>
                        <center>
                            <h3>No Data For This Filter</h3>
                        </center>
                    </div>
                )
            }
        } else{
            return(
                <div>
                    <img src="/images/loader.gif"/>
                </div>
            )
        }
    } 

   
    return (
            <div>               
                {RenderRest(props)}               
            </div>
    )
}

export default RestListDisplay
