import React from 'react'
import { Link } from 'react-router-dom'


const Home_quickSearchDisplay = (props) => {
    const renderMealtype = ({mealtype}) => {
        if (mealtype) {
            
                return (
                    mealtype.map((data) => {
                        return(
                            <Link to={`/list/${data.mealtype}`}>
                                <div className="col-sm-6 col-md-4 col-lg-4">
                                    <div className="tilecontainer">
                                        <div className="row">
                                            <div className="col-xs-5 col-sm-5 col-md-5 col-lg-5">
                                                <img src={`./images/${data.name}.png`} className="img-responsive border-radius"></img>
                                            </div>
                                            <div className="col-xs-7 col-sm-7 col-md-7 col-lg-7">
                
                                                <p className="tileheading">
                                                    {data.name}
                                                </p>
                                                <span className="tilesubheading">Start your day with exclusive breakfast option</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        )
                    })
                )
        } else{
            return(
                <div>
                    <img src="/images/loader.gif"/>
                </div>
            )
        }
    }
    return (
        <div className="container">
            <h1 className="heading">Quick Search</h1>
            <p className="subheading">
                Discover your restaurant by type of meal
            </p>
            <div className="row">
                {renderMealtype(props)}
            </div>
        </div>
    )
}

export default Home_quickSearchDisplay
